/**
 * Internal block libraries
 */

const { __ } = wp.i18n;

const {
	PluginDocumentSettingPanel
} = wp.editPost;

const {
	PanelBody,
	SelectControl,
	Button,
	Animate
} = wp.components;

const {
	Component,
	Fragment
} = wp.element;

const { compose} = wp.compose;

const { withSelect, withDispatch } = wp.data;

const { registerPlugin } = wp.plugins;

/*********************************************/
/**
 * End Internal block libraries
 */


class SHWCP_Database_Choice extends Component {

	constructor() {
		super( ...arguments );
		//console.log(this.props);

		this.onChangeTemplate = this.onChangeTemplate.bind( this );

		this.state = {
			key: 'wcp_db_select',
			value: 'default',
		}

		wp.apiFetch( { path: `/wp/v2/pages/${this.props.postId}`, method: 'GET' } ).then(
			( data ) => {
				this.setState( { 
					value: data.meta.wcp_db_select
				} );
				return data;
			},
			( err ) => {
				return err;
			},
		);
	}

	// Updates database meta wcp_db_select for page
	static getDerivedStateFromProps( nextProps, state ) {
		if ( ( nextProps.isPublishing || nextProps.isSaving ) && !nextProps.isAutoSaving ) {
			wp.apiRequest( { path: `/shwcpdb/v1/update-meta?id=${nextProps.postId}`, method: 'POST', data: state } ).then(
				( data ) => {
					return data;
				},
				( err ) => {
					return err;
				}
			);
		}	
	}

	// This function ties in to the select list to make available the WordPress update button on change
	// and updates the value
	onChangeTemplate( value ) {
		this.setState( { value } );
		this.props.editPostStatus( { edited: true } );
	}

	render() {
		return (
			<Fragment>
				// Conditional to Only render this for our template
				{ 'wcp-fullpage-template.php' == this.props.pageTemplate && (

				<PluginDocumentSettingPanel
        			name="custom-panel"
        			title={__("WP Contacts Options", "shwcp")}
        			className="custom-panel"
    			>

					<SelectControl
        				label={ __( 'Select WP Contacts Database:', 'shwcp' ) }
        				value={this.state.value}
        				onChange={this.onChangeTemplate}
						// This value comes from our localized script making the database list available in js
        				options = {wcp_databases.databases}
    				/>
					
    			</PluginDocumentSettingPanel>	

				) }
			</Fragment>
		)
	}
}

const HOC = compose( [
	withSelect( ( select, { forceIsSaving } ) => {
	const {
		getCurrentPostId,
		isSavingPost,
		isPublishingPost,
		isAutosavingPost,
		getEditedPostAttribute,
	} = select( 'core/editor' );
	return {
		postId: getCurrentPostId(),
		pageTemplate: getEditedPostAttribute('template'),
		isSaving: forceIsSaving || isSavingPost(),
		isAutoSaving: isAutosavingPost(),
		isPublishing: isPublishingPost(),
	};
} ),
	withDispatch( (dispatch ) => ( {
		editPostStatus: dispatch( 'core/editor' ).editPost,
	} )),
] )( SHWCP_Database_Choice );

registerPlugin( 'shwcp-database-choice', {
	icon: 'admin-settings',
	render: HOC,
} );
